import { apiSlice } from '../../App.apiSlice'
import * as I from './fitting.interface'

export const fittingApi = apiSlice.injectEndpoints({
  endpoints: (build) => ({
    // ÏÇ¨Ïö© ÏïàÌï† api
    getAllFitting: build.query<any, void>({
      query: () => ({
        url: `/beauties`,
      }),
      providesTags: ['fitting'],
    }),

    getProductFitting: build.query<Array<I.Res_FittingDetail>, { product_id: number }>({
      query: ({ product_id }) => ({
        url: `/beauties/${product_id}/`,
      }),
      providesTags: ['fitting'],
    }),

    getFittingDetail: build.query<any, I.Req_FittingDetail>({
      query: ({ beauty_id }) => ({
        url: `/beauties/${beauty_id}/`,
      }),
      providesTags: ['fitting'],
    }),

    postFitting: build.mutation<any, I.Req_PostFitting>({
      query: (params) => ({
        url: `/beauties`,
        method: 'POST',
        body: params,
      }),
      invalidatesTags: ['fitting'],
    }),

    deleteFitting: build.mutation<any, I.Req_FittingDetail>({
      query: ({ beauty_id }) => ({
        url: `/beauties/${beauty_id}/`,
        method: 'DELETE',
      }),
      invalidatesTags: ['fitting'],
    }),
  }),
})

export const {
  useGetAllFittingQuery,
  usePostFittingMutation,
  useGetProductFittingQuery,
  useGetFittingDetailQuery,
  useDeleteFittingMutation,
} = fittingApi
